/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import { rhythm } from '../utils/typography';
import linkedinIcon from '../../content/assets/linkedin-icon.png';
import instagramIcon from '../../content/assets/instagram-icon.png';

const Bio = ({ mode = 'author' }) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/bio-pic.png/" }) {
        childImageSharp {
          fixed(width: 80, height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            instagram
            linkedin
          }
          siteUrl
        }
      }
    }
  `);

  const { author, social, siteUrl } = data.site.siteMetadata;
  return (
    <div
      style={{
        display: `flex`,
      }}
    >
      {/* <Image
        alt={author.name}
        style={{
          marginRight: rhythm(1 / 2),
          marginBottom: 0,
          minWidth: 50,
          borderRadius: `100%`,
        }}
        imgStyle={{
          borderRadius: `50%`,
        }}
      /> */}
      <p style={{ marginTop: `auto` }}>
        {mode === 'author' && (
          <>
            Written by <a href="/about">{author.name}</a>
          </>
        )}
        <br></br>
        {` `}
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <img
            src={instagramIcon}
            alt="insta"
            style={{ width: '30px', marginBottom: '0px' }}
          />
          <a href={`https://instagram.com/${social.instagram}`}>Instagram</a>
        </div>

        <div style={{ display: 'flex', alignItems: 'center' }}>
          <img
            src={linkedinIcon}
            alt="insta"
            style={{ width: '30px', marginBottom: '0px' }}
          />
          <a href={social.linkedin}>Linkedin</a>
        </div>
      </p>
    </div>
  );
};

export default Bio;
